* {
  transition: 0.2s ease;
  box-sizing: border-box;
}
.parent {
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(0deg, rgba(56, 86, 110, 1) 0%, rgba(13, 62, 54, 1) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}
.card {
  height: 25rem;
  position: relative;
  width: 15rem;
  border-radius: 2rem;
  padding: 1rem;
  background-image: linear-gradient(34deg, rgba(0, 0, 0, 1) 0%, rgb(116, 15, 0) 100%);
  z-index: 4;
}

.card > h1 {
  text-align: center;
  color: rgb(4, 158, 81);
  margin: 2rem 0;
  font-family: "Poppins", sans-serif;
}
.card hr {
  border: none;
  width: 40%;
  margin: 1.5rem auto;
}
.card > p {
  text-align: center;
  font-family: "Lato";
  color: rgb(130, 130, 130);
}
.card:hover {
  box-shadow: 0 0 40px rgb(0, 0, 0);
}
.team {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  color: white;
  z-index: 2;
  height: 7rem;
  width: 7rem;
  background-color: rgb(255, 255, 255);
  border-radius: 100%;
  overflow: hidden;
}
.pfp {
  height: 7rem;
  width: 7rem;
  display: block;
  border-radius: 100%;
  margin: auto;
}
.team:hover {
  height: 25rem;
  width: 20rem;
  border-radius: 2rem;
  z-index: 100;
  box-shadow: 0 0 20px black;
  padding: 2rem;
}
.team h1 {
  display: block;
  color: rgb(76, 76, 76);
  text-align: center;
}
